@import url("https://fonts.googleapis.com/css2?family=Poppins&display=swap");

.navbar {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: transparent;
  opacity: 1;
  padding-right: 5vw;
  margin-top: 1vh;
  margin-bottom: 30px;
  position: fixed;
  top: 0;
  right: 0.1px;
  width: 100%;
  transition: 0.3s;
  z-index: 10;
}

.navbarScrolled {
  list-style-type: none;
  margin: 0;
  padding: 0;
  overflow: hidden;
  background-color: rgb(255, 255, 255);
  opacity: 1;
  padding-right: 5vw;
  box-shadow: 0px 3px 12px #00000029;
  position: fixed;
  top: 0;
  right: 0.1px;
  width: 100%;
  transition: 0.3s;
  z-index: 3;
}

li {
  float: right;
}

.link {
  display: block;
  padding: 30px 16px;
  color: #414141;
  font-weight: 600;
  opacity: 0.6;
  text-decoration: none;
  font-size: 1.2rem;
  font-family: "Poppins", sans-serif;
}

li a:hover {
  color: black;
}

.img {
  margin-left: 10vw;
  width: 200px;
  height: 60px;
  transition: 0.3s;
}

.imgScrolled {
  margin-left: 12vw;
  width: 190px;
  height: 57px;
  transition: 0.3s;
}

html {
  scroll-behavior: smooth;
}

.botao {
  display: none;
  z-index: 100;
}

.lateralNavbar {
  display: none;
}

@media only screen and (max-width: 1200px) {
  .navbar {
    background-color: transparent;
  }
  .navbarScrolled {
    overflow: hidden;
    background-color: rgb(255, 255, 255);
    opacity: 1;
    padding-right: 5vw;
    box-shadow: 0px 3px 12px #00000029;
    position: fixed;
    top: 0;
    right: 0.1px;
    width: 100%;
    transition: 0.5s;
  }

  .lateralNavbar {
    display: none;
  }

  .link {
    display: block;
    padding: 30px 8px;
    color: #414141;
    opacity: 0.6;
    text-decoration: none;
    font-size: 1rem;
    font-family: "Poppins", sans-serif;
    transition: 0.5s;
  }

  .img {
    margin-left: 10vw;
    width: 220px;
    height: 60px;
  }

  .imgScrolled {
    margin-top: 0.4vh;
    width: 200px;
    transition: 0.1s;
  }
}

@media only screen and (max-width: 700px) {
  .navbar {
    background-color: transparent;
  }

  .navbarScrolled {
    overflow: hidden;
    background-color: rgb(255, 255, 255);
    opacity: 1;
    padding-right: 5vw;
    box-shadow: 0px 3px 12px #00000029;
    position: fixed;
    top: 0;
    right: 0.1px;
    width: 100%;
    height: 8vh;
    transition: 0.3s;
  }

  .link {
    display: none;
  }

  .botao {
    display: block;
    height: 8vh;
    width: 10vw;
    margin-left: 85vw;
    position: fixed;
    top: 0;
    z-index: 100;
  }

  .actionBotao {
    outline: none;
    background-color: transparent;
    border: none;
    position: absolute;
    z-index: 4;
    margin-top: 7px;
    height: 6vh;
    width: 9vw;
  }

  .imgBotao {
    margin-top: 2.5vh;
    height: 3vh;
  }

  .img {
    margin-left: 10vw;
    width: 160px;
    height: 55px;
    margin-top: -1vh;
  }

  .imgScrolled {
    width: 130px;
    height: 45px;
    padding-top: 1vh;
    transition: 0.1s;
  }

  .secondBackground {
    display: contents;
    background-color: transparent;
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    z-index: 3;
  }

  .secondBackgroundActived {
    background-color: rgb(0, 0, 0);
    opacity: 0.4;
    height: 100vh;
    width: 100vw;
    position: fixed;
    top: 0;
    transition: 0.5s;
    z-index: 3;
  }

  .lateralNavbar {
    list-style-type: none;
    height: 100vh;
    width: 50vw;
    display: none;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    margin-top: 0;
    margin-left: 100vw;
    padding-left: 0;
    padding-top: 15vh;
    position: fixed;
    border: 2px solid rgb(214, 214, 214);
    z-index: 4;
    top: 0;
    right: 0;
    left: 0;
    transition: 0.2s;
  }

  .lateralNavbarActived {
    list-style-type: none;
    height: 100vh;
    width: 50vw;
    display: flex;
    flex-direction: column;
    background-color: rgb(255, 255, 255);
    margin-top: 0;
    margin-left: 50vw;
    padding-left: 0;
    padding-top: 15vh;
    position: fixed;
    border: 2px solid rgb(214, 214, 214);
    z-index: 5;
    top: 0;
    right: 0;
    left: 0;
    transition: 0.5s;
  }

  .linkLateral {
    display: block;
    padding: 3vh 7vw;
    color: #9e9e9e;
    text-decoration: none;
    font-size: 1.4rem;
    font-family: "Poppins", sans-serif;
  }

  .buttonSidebar {
    background-color: transparent;
    border: none;
    width: 50vw;
  }

  .buttonSidebarSocialNetwork {
    width: 30%;
    height: 40%;
    border: none;
    background-color: transparent;
    padding: 0;
    outline: none;
  }

  .imgLogoSideBarSocialNetwork {
    width: 70%;
    height: 70%;
  }

  .boxSidebarSocialNetwork {
    display: flex;
    justify-content: center;
    align-items: center;
    height: 20%;
    padding-top: 10vh;
  }
}
