.Services .slick-dots {
  display: flex !important;
  justify-content: center;
  width: 100%;
  padding: 0;
  margin-top: 100px;
  list-style: none;
  text-align: center;
}

.Services .slick-dots li {
  content: none;
  height: 10px;
  width: 10px;
  background-color: #414141;
  opacity: 0.15;
  border-radius: 50%;
  box-shadow: 2px 2px 4px #00222d33;
  transform: scale(1.2);
}

.Services .Slider .slick-dots .slick-active {
  background-color: #414141;
  opacity: 0.35;
  box-shadow: none;
  transform: scale(1.8);
}

.Services .slick-dots li button:before {
  content: none;
}

.Services .slick-prev {
  transform: scaleX(-1) scale(0.8);
}

.Services .slick-next {
  transform: scale(0.8);
}

.Services .slick-prev:before {
  content: url(/static/media/arrow.75b5925f.svg);
  transform: rotate(180deg) !important;
}
.Services .slick-next:before {
  content: url(/static/media/arrow.75b5925f.svg);
}

.blank-space {
