.widget-input-button {
    width: 100%;
    display: flex;
    flex-direction: column;
}

.widget-input {
    width: 100%;
    font-size: 1.2rem;
    box-sizing: border-box;
    height: 40px;
    border: 2px solid #DFDFDF;
    padding: 5px 5px;
    padding-left: 15px;
    border-radius: 15px;
    outline: none;
}

.widget-submit-message {
    display: flex;
    align-items: center;
    justify-content: center;
    flex-direction: row-reverse;
    background-color: #FFDC00;
    border-radius: 0px 15px 15px 0px;
    border: none;
    align-self: flex-end;
    height: 40px;
    width: 35px;
    margin-top: -40px;
    border: 2px solid #DFDFDF;
    border-left: none;
    cursor: pointer;
    outline: none;
}

@media only screen and (max-width: 700px) {

    .widget-text {
        font-size: 1rem;
    }

    .button-widget {
        width: 55px;
        height: 55px;
    }

    .widget-chat {
        width: 230px;
    }

}


