.footer {
  background: #414141;
  opacity: 0.5;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0 10vw;
}

.footer-text {
  display: flex;
  align-items: center;
}

.container-footer {
  display: flex;
  flex-direction: column;
  width: 100%;
}

.white {
  width: 100%;
  display: flex;
  justify-content: space-around;
  align-items: flex-end;
  margin-bottom: 50px;
  height: 150px;
}

.address {
  max-width: 230px;
  color: #707070;
}

.footer-titles {
  color: #414141;
  font-weight: 600;
  font-size: 1.2rem;
}

.img-social-media {
  width: 30px;
  height: 30px;
  margin-bottom: 10px;
}

.social-media {
  display: flex;
  justify-content: space-evenly;
}

.row-footer {
  display: flex;
  align-items: center;
  color: #707070;
}

.margin-icon {
  margin-left: 10px;
  margin-right: 10px;
  width: 15px;
  height: 15px;
}

#citi-icon {
  margin-left: 5px;
  margin-right: 5px;
  width: 25px;
  height: 25px;
}

.button-footer {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

@media (max-width: 700px) {
  .footer-titles {
    font-size: 1rem;
  }
  .footer {
    flex-wrap: wrap;
    width: 100%;
    justify-content: space-around;
    padding: 0;
  }

  .white {
    width: 80%;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    height: 200px;
    padding-left: 10vw;
    box-sizing: border-box;
  }
  .up {
    order: -1;
    justify-self: flex-end;
    margin-left: 50vw;
    margin-top: -18vh;
  }
}
