.container-dc {
  display: flex;
  justify-content: center;
  margin-top: 12vh;
}

.card-dc {
  background: #ffffff 0% 0% no-repeat padding-box;
  border: 3px solid #dfdfdf;
  border-radius: 13px 13px 15px 15px;
  box-shadow: 0px 3px 12px #00000029;
  width: 50vw;
  border-top: 5px solid #00005a;
  min-height: max-content;
}

.column {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  position: relative;
  bottom: 80px;
}

.image-box-dc {
  border-radius: 50%;
  border: 3px solid #dfdfdf;
  width: 170px;
  height: 170px;
  overflow: hidden;
}

.blue-dc {
  background: #00005a;
  width: 170px;
  height: 170px;
  border-radius: 50%;
  position: absolute;
  z-index: 1;
  opacity: 0.2;
}

.depoiments-image {
  width: 100%;
  object-fit: cover;
}

.client-name-text {
  font-size: 2.5rem;
  font-weight: 600;
  color: #414141;
  margin-top: 20px;
}

.company-function-text {
  font-size: 1.7rem;
  color: #414141;
  margin-top: -25px;
}

.depoiment-text {
  font-size: 1.2rem;
  color: #707070;
  max-width: 32vw;
  line-height: 25px;
  min-height: max-content;
}

.yellow-box-dc-container {
  width: 100%;
  height: 0;
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.yellow-box-dc {
  background-color: #ffdc00;
  display: flex;
  flex-direction: column;
  width: 60px;
  margin-bottom: -400px;
  border-bottom-left-radius: 10px;
  border-top-left-radius: 10px;
}

.img-social-media-dc {
  width: 40px;
  height: 40px;
  margin-bottom: 10px;
  margin-top: 10px;
  z-index: 1;
}

.button-dc {
  display: flex;
  justify-content: center;
  align-items: center;
  background-color: transparent;
  border: none;
  cursor: pointer;
  outline: none;
}

.yellow-line {
  width: 80px;
  height: 3px;
  background-color: #ffdc00;
  margin-top: -5px;
}

.quote-box {
  position: relative;
  top: 170px;
  left: 80px;
  height: 0;
}

.quote-img {
  width: 6vw;
  opacity: 0.9;
}

@media (max-width: 700px) {
  .card-dc {
    width: 65vw;
    height: vh;
  }

  .column {
    bottom: 60px;
  }

  .image-box-dc {
    width: 100px;
    height: 100px;
    z-index: 1;
  }

  .blue-dc {
    width: 100px;
    height: 100px;
  }

  .client-name-text {
    font-size: 1.3rem;
    margin-top: 20px;
  }

  .company-function-text {
    font-size: 1.2rem;
    margin-top: -15px;
  }

  .depoiment-text {
    font-size: 1rem;
    max-width: 50vw;
    line-height: 20px;
  }

  .yellow-box-dc {
    background-color: white;
    width: 30px;
    margin-bottom: -120px;
  }

  .img-social-media-dc {
    width: 20px;
    height: 20px;
    margin-bottom: 5px;
    margin-top: 5px;
  }

  .yellow-line {
    width: 50px;
    height: 2px;
    margin-top: -5px;
  }

  .quote-box {
    top: 135px;
    left: 10px;
    height: 0;
  }

  .quote-img {
    width: 13vw;
    opacity: 0.7;
  }
}
