.background {
  display: flex;
  height: 100vh;
  justify-content: flex-end;
}

.background1 {
  display: flex;
  align-items: center;
  background-color: white;
  height: 100vh;
  width: 50vw;
}

.imgContato {
  height: 80vh;
  width: 50vw;
  border-top-right-radius: 10px;
  border-bottom-right-radius: 10px;
  position: absolute;
  z-index: 1;
  object-fit: cover;
}

.background2 {
  display: flex;
  justify-content: center;
  background-color: rgb(226, 226, 226);
  border-top-left-radius: 10px;
  border-bottom-left-radius: 10px;
  height: 100vh;
  width: 60vw;
}

.cantainerInfo {
  margin-top: 8vh;
  height: 20vh;
  width: 40vw;
  padding-left: 15vw;
  font-family: "Poppins";
}

.descricao1 {
  font-size: 1.2rem;
  color: #414141;
  width: 20vw;
}

.descricao2 {
  font-size: 4rem;
  font-weight: 600;
  color: #2e2e2e;
  margin-top: 0;
  margin-bottom: 5vh;
}

.descricao3 {
  font-size: 1.2rem;
  color: #707070;
  width: 25vw;
}

.caixaInput {
  display: flex;
  flex-direction: column;
  width: 22vw;
}

.inputName {
  margin-top: 3vh;
  margin-bottom: 1vh;
  border-radius: 10px;
  border: solid 2px rgb(226, 226, 226);
  height: 5vh;
  font-size: 1.2rem;
  padding-left: 1vw;
  padding-right: 1vw;
  outline: none;
}

.inputNameEmpresa {
  margin-bottom: 1vh;
  border-radius: 10px;
  border: solid 2px rgb(226, 226, 226);
  height: 5vh;
  font-size: 1.2rem;
  padding-left: 1vw;
  padding-right: 1vw;
  outline: none;
}

.inputEmail {
  margin-bottom: 1vh;
  border-radius: 10px;
  border: solid 2px rgb(226, 226, 226);
  height: 5vh;
  font-size: 1.2rem;
  padding-left: 1vw;
  padding-right: 1vw;
  outline: none;
}

.inputTelefone {
  margin-bottom: 1vh;
  border-radius: 10px;
  border: solid 2px rgb(226, 226, 226);
  height: 5vh;
  font-size: 1.2rem;
  padding-left: 1vw;
  padding-right: 1vw;
  outline: none;
}

.inputMensagem {
  margin-bottom: 1vh;
  border-radius: 10px;
  border: solid 2px rgb(226, 226, 226);
  height: 10vh;
  font-size: 1.2rem;
  padding-left: 1vw;
  padding-right: 1vw;
  padding-top: 1vh;
  outline: none;
  resize: none;
}

textarea {
  font-family: "Poppins", sans-serif;
  font-size: 1.2rem;
  outline: none;
}

.buttonEnviar {
  margin-left: 10vw;
  margin-top: 5vh;
  border-radius: 30px;
  border: none;
  background-color: #ffdc00;
  box-shadow: 2px 2px rgb(196, 196, 196);
  width: 12vw;
  height: 6vh;
  font-size: 1.3rem;
  color: #414141;
  cursor: pointer;
  outline: none;
}

.popup-enviado {
  position: fixed;
  z-index: 9;
  top: 0;
  right: 0;
  margin-top: 100px;
  margin-right: 100px;
  width: 300px;
  height: 50px;
  transition: 1s;
}

.popup-enviado-desactived {
  position: fixed;
  z-index: -1;
  top: 0;
  right: 0;
  margin-top: 100px;
  margin-right: 100px;
  width: 300px;
  height: 50px;
  opacity: 0;
  transition: 1s;
}

.botaopopup {
  border: none;
  cursor: pointer;
  box-shadow: 0px 3px 12px #00000029;
  background-color: rgb(67, 69, 226);
  color: white;
  width: 300px;
  height: 50px;
  border-radius: 10px;
  outline: none;
  transition: 1s;
}

.botaopopup-desactived {
  border: none;
  cursor: pointer;
  box-shadow: 0px 3px 12px #00000029;
  background-color: rgb(67, 69, 226);
  color: white;
  width: 300px;
  height: 50px;
  border-radius: 10px;
  outline: none;
  opacity: 0;
  transition: 1s;
}

.popup-erro {
  position: fixed;
  z-index: 9;
  top: 0;
  right: 0;
  margin-top: 100px;
  margin-right: 100px;
  width: 300px;
  height: 50px;
  transition: 1s;
}

.popup-erro-desactived {
  position: fixed;
  z-index: -1;
  top: 0;
  right: 0;
  margin-top: 100px;
  margin-right: 100px;
  width: 300px;
  height: 50px;
  opacity: 0;
  transition: 1s;
}

.botaopopup-erro {
  border: none;
  cursor: pointer;
  box-shadow: 0px 3px 12px #00000029;
  background-color: rgb(248, 54, 54);
  color: white;
  width: 300px;
  height: 50px;
  border-radius: 10px;
  outline: none;
  transition: 1s;
}

.botaopopup-erro-desactived {
  border: none;
  cursor: pointer;
  box-shadow: 0px 3px 12px #00000029;
  background-color: rgb(248, 54, 54);
  color: white;
  width: 300px;
  height: 50px;
  border-radius: 10px;
  outline: none;
  opacity: 0;
  transition: 1s;
}

